//
// @section Typography
// 
// Defines the styling of text elements
//
// @package   	WordPress
// @subpackage 	TrendPress
// @author    	Trendwerk
// @license   	GPL v2
//

// @section    Typography
// @subsection Render settings

html {
	-webkit-font-smoothing: antialiased;
	-webkit-text-size-adjust: none;
}

// @section    Typography
// @subsection Content

body {
	color: $text-color; 
	font-family: $text-font;
	font-size: 14px;
	line-height: $line-height;
}

// @section    Typography
// @subsection Headings

h1,
h2,
h3,
h4,
h5,
h6 {
	color: $heading-color;
	font-family: $heading-font;
	font-weight: 700;
	line-height: 1.325em;
	margin: 0 0 $line-height;
}

h1 {
	font-size: 28px;

	@media (min-width: 641px) {
		font-size: 40px;
	}
}

h2 {
	font-size: 20px;

	@media (min-width: 641px) {
		font-size: 28px;
	}
}

h3 {
	font-size: 18px;

	@media (min-width: 641px) {
		font-size: 22px;
	}
}

h4 {
	font-size: 16px;

	@media (min-width: 641px) {
		font-size: 18px;
	}
}

h5 {
	font-size: 14px;

	@media (min-width: 641px) {
		font-size: 16px;
	}
}

h6 {
	font-size: 12px;

	@media (min-width: 641px) {
		font-size: 14px;
	}
}

// @section    Typography
// @subsection Links

a {
	color: $link-color;
	outline: 0;
	text-decoration: underline;

	&:active,
	&:focus,
	&:hover { 
		color: darken($link-color, 15%);
		text-decoration: underline;
	}

	// more link

	&.more-link {
		white-space: nowrap;
	}

	// add characters to link

	&.more-link:after,
	&.back:before,
	&.prev:before,
	&.next:after {
		display: inline-block;
		font-family: fontawesome;
		font-size: 9px;
	}

	&.back:before,
	&.prev:before {
		content: '\f053';
		padding-right: 4px;
	}

	&.more-link:after,
	&.next:after {
		content: '\f054';
		padding-left: 4px;
	}

	// call to action button

	&.button {
		@extend .button-style;

		// secondary button

		&.secondary {
			background: $secondary-color;
			color: $white;

			&:active,
			&:focus,
			&:hover {
				background: darken($secondary-color, 15%);
			}
		}
	}

	// style for tag

	&[rel='tag'] {
		background: #ccc;
		border-radius: 3px;
		color: $text-color;
		display: inline-block;
		font-size: 12px;
		line-height: 25px;
		margin: 0 10px 10px;
		padding: 0 10px;
		text-decoration: none;
		text-transform: lowercase;

		&:active,
		&:focus,
		&:hover {
			background: $heading-color;
			color: $white;
		}
	}
}

// @section    Typography
// @subsection Paragraphs

p {
	margin: 0 0 $line-height;
}

// @section    Typography
// @subsection Text styles

strong,
b {
	font-weight: bold;
}

em,
i {
	font-style: italic;
}

// @section    Typography
// @subsection Lists

ul { 
	list-style-position: outside;
	margin: 0 0 $line-height 20px;

	ul {
		margin: 0 0 0 20px;
	}
}

ol {
	list-style-position: outside;
	list-style-type: decimal;
	margin: 0 0 $line-height 20px;

	ol {
		list-style-type: lower-alpha;
		margin: 0 0 0 20px;
	}
}

// @section    Typography
// @subsection Tables

table { 
	border: 1px solid $border-color;
	border-collapse: collapse;
	border-spacing: 0;	
	margin: 0 0 $line-height;
	width: 100%;

	tr { 
		border-bottom: 1px solid $border-color;
		border-right: 1px solid $border-color;

		&:nth-child(even) {
			background: $light-background;
		}

		td,
		th { 
			border-right: 1px solid $border-color;
			padding: 5px 10px;
			text-align: left;
		}

		th { 
			font-weight: bold;
		}
	}

	thead tr {
		background: $light-background;
	}
}

// @section    Typography
// @subsection Quotes

blockquote {
	color: $text-light;
	display: inline-block;
	font-size: 16px;
	font-style: italic;
	margin: 0 0 $line-height;
	text-align: center;
	width: 100%;

	p {
		display: inline;
		margin: 0;
	}

	cite {
		display: block;
		margin-top: 10px;
	}
}

cite {
	color: $text-color;
	display: block;
	font-style: normal;
	font-weight: bold;
	margin: 5px 0 10px; 

	a {
		color: $text-color;
	}
}

// @section    Typography
// @subsection Code snippets

code,
pre {
	background: #ffefb4;
	color: #333;
	font-size: 13px;
}
	
pre {
	margin-bottom: 20px; 
	padding: 20px; 
	white-space: pre-wrap;
	white-space: -moz-pre-wrap;
	white-space: -o-pre-wrap;
}

// @section    Typography
// @subsection Abbreviations

acronym,
abbr {
	border-bottom: 1px dotted $text-color;
	cursor: help;

	&:active,
	&:focus,
	&:hover {
		border-bottom-style: solid;
	}
}

// @section    Typography
// @subsection Deleted and inserted text 

del {
	color: $text-light;
	text-decoration: line-through;
}

ins {
	text-decoration: none;
}

// @section    Typography
// @subsection Selection 

::-moz-selection,
::selection {
	background-color: $primary-color;
	color: $white;
}

// @section    Typography
// @subsection Misc

hr {
	border: 0;
	border-top: 1px solid $border-color;
	display: block;
	height: 1px;
	margin: 0 0 $line-height;
	padding: 0;
}

.telephone {
	color: $heading-color;
	font-size: 24px;
	font-weight: bold;

	&:before {
		content: '\f095';
		font-family: FontAwesome;
		font-size: 30px;
		display: inline-block;
		margin: 0 7px 0;
		position: relative;
		top: 4px;
	}

	a[href^=tel] {
		color: $heading-color;
		text-decoration: none;
	}
}
