//
// @section Mixins
//
// Mixins allow you to embed all the properties of a class into another class 
// by simply including the class name as one of its properties. It’s just like 
// variables, but for whole classes. Mixins can also behave like functions, 
// and take arguments.
//
// @package   	WordPress
// @subpackage 	TrendPress
// @author    	Trendwerk
// @license   	GPL v2
// 

//
// @section 	Mixins
// @subsection  Animate 

@mixin animate-this( $what: all, $time: 0.5s, $ease: ease-in-out ) {
	-webkit-transition: $what $time $ease;
	transition: $what $time $ease;
}

// @section    Mixins
// @subsection Collumns

@mixin columns( $number, $gap ) {
	-moz-column-count: $number;
	-webkit-column-count: $number;
	column-count: $number;
	-moz-column-gap: $gap;
	-webkit-column-gap: $gap;
	column-gap: $gap;
}

// @section    Mixins
// @subsection Font Awesome
// 
// website: http://fontawesome.io/icons/
// cheatsheet: http://fortawesome.github.io/Font-Awesome/cheatsheet/

@mixin font-awesome($position,$glyph) {
	position: relative;

	@if $position == before {
		&:before {
			content: $glyph;
			font-family: FontAwesome;
			font-style: normal;
			font-weight: normal;
			text-decoration: inherit;
		}

	} @else if $position == after {
		&:after {
			content: $glyph;
			font-family: FontAwesome;
			font-style: normal;
			font-weight: normal;
			text-decoration: inherit;
		}
	}
}

// @section    Mixins
// @subsection Gradient

@mixin gradient($direction,$startColor,$endColor) {
	background-color: $startColor;

	@if $direction == horizontal {
		background-image: -webkit-gradient(linear, left top, right top, from($startColor), to($endColor));
		background-image: -webkit-linear-gradient(left, $startColor, $endColor);
		background-image: -moz-linear-gradient(left, $startColor, $endColor);
		background-image: -ms-linear-gradient(left, $startColor, $endColor);
		background-image: -o-linear-gradient(left, $startColor, $endColor);

    } @else if $direction == vertical {
		background-image: -webkit-gradient(linear, left top, left bottom, from($startColor), to($endColor));
		background-image: -webkit-linear-gradient(top, $startColor, $endColor);
		background-image: -moz-linear-gradient(top, $startColor, $endColor);
		background-image: -ms-linear-gradient(top, $startColor, $endColor);
		background-image: -o-linear-gradient(top, $startColor, $endColor);
    }
}

// @section    Mixins
// @subsection Placeholder

@mixin placeholder($out,$on) {

	&:-moz-placeholder {
		color: $out;
	}

	&::-moz-placeholder {
		color: $out;
	}

	&:-ms-input-placeholder {
		color: $out;
	}

	&::-webkit-input-placeholder {
		color: $out;
	}
	
	&:focus:-moz-placeholder {
		color: $on;
	}

	&:focus::-moz-placeholder {
		color: $on;
	}

	&:focus:-ms-input-placeholder {
		color: $on;
	}

	&:focus::-webkit-input-placeholder {
		color: $on;
	}
}

// @section    Mixins
// @subsection Sprites

@mixin sprite($top,$left) {
	background-image: url('../../../assets/images/sprite-x1.png');
	background-position: $top $left;
	background-repeat: no-repeat;

		@media 
		(-webkit-min-device-pixel-ratio: 2), 
		(min-resolution: 192dpi) { 
		background-image: url('../../../assets/images/sprite-x2.png');
		background-position: $top x 2 $left x 2;
		background-size: 500px 500px;
	}
}
