// @section    Extends
// @subsection Button

.button-style {
	background: $primary-color;
	border-radius: 3px;
	color: $white;		
	display: inline-block;
	font-weight: bold;
	height: 50px;
	line-height: 50px;
	margin: 0 10px 10px;
	padding: 0 35px;
	position: relative;
	text-decoration: none;
	text-transform: uppercase;
	white-space: nowrap;

	&:active {
		top: 1px;
	}

	&:active,
	&:focus,
	&:hover { 
		background: darken($primary-color, 15%);
		color: $white;
		outline: none;
		text-decoration: none;
	}	
}

// @section    Extends
// @subsection Ellipsis

.ellipsis {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
